import React, { useRef, useState, useEffect } from 'react';
import { Canvas, Image as FabricImage, PencilBrush } from 'fabric';

const ImageInpainting = () => {
    const canvasRef = useRef(null);
    const fabricCanvasRef = useRef(null); // Holds the Fabric.js canvas instance
    const [brushSize, setBrushSize] = useState(5);

    useEffect(() => {
        // Initialize Fabric.js Canvas
        const canvas = new Canvas(canvasRef.current, {
            isDrawingMode: false,
            selection: false,
        });
        fabricCanvasRef.current = canvas;

        // Set canvas size
        canvas.setWidth(800);
        canvas.setHeight(600);

        // Clean up on component unmount
        return () => {
            canvas.dispose();
        };
    }, []);

    const handleImageUpload = (event) => {
        const file = event.target.files[0];
        if (!file) return;

        const reader = new FileReader();
        reader.onload = (e) => {
            const imgElement = new Image();
            imgElement.src = e.target.result;

            imgElement.onload = () => {
                const canvas = fabricCanvasRef.current;
                const imgInstance = new FabricImage(imgElement);

                // Scale the image to fit the canvas dimensions
                const scaleX = canvas.width / imgElement.width;
                const scaleY = canvas.height / imgElement.height;

                imgInstance.scaleX = scaleX;
                imgInstance.scaleY = scaleY;
                imgInstance.left = 0;
                imgInstance.top = 0;

                // Add the image to the canvas as an object
                canvas.add(imgInstance);
                canvas.renderAll(); // Re-render the canvas to display the image
            };
        };
        reader.readAsDataURL(file);
    };

    const handleBrushSizeChange = (event) => {
        const newSize = parseInt(event.target.value, 10);
        setBrushSize(newSize);

        if (fabricCanvasRef.current) {
            fabricCanvasRef.current.freeDrawingBrush.width = newSize;
        }
    };

    const startDrawing = () => {
        if (fabricCanvasRef.current) {
            const canvas = fabricCanvasRef.current;
            canvas.isDrawingMode = true;

            const pencilBrush = new PencilBrush(canvas);
            pencilBrush.color = 'white';
            pencilBrush.width = brushSize;

            canvas.freeDrawingBrush = pencilBrush;
        }
    };

    const exportMask = () => {
        if (fabricCanvasRef.current) {
            const maskDataUrl = fabricCanvasRef.current.toDataURL({
                format: 'png',
                multiplier: 1,
            });

            // Create a download link
            const link = document.createElement('a');
            link.href = maskDataUrl;
            link.download = 'mask.png';
            link.click();
        }
    };

    return (
        <div className="image-inpainting-container">
            <h1>Image Inpainting Widget</h1>
            <input type="file" accept="image/*" onChange={handleImageUpload} />
            <br />
            <canvas ref={canvasRef} className="fabric-canvas" />
            <br />
            <label>
                Brush Size:
                <input
                    type="range"
                    min="1"
                    max="50"
                    value={brushSize}
                    onChange={handleBrushSizeChange}
                />
            </label>
            <button onClick={startDrawing}>Start Drawing</button>
            <button onClick={exportMask}>Export Mask</button>
        </div>
    );
};

export default ImageInpainting;
